/*
Video Component

Styleguide 6.0
*/

/*
Hyperlink

markup:
<div class="container video-component">
  <div class="heading">
    <h1>This is the heading for the video</h1>
  </div>
  <div class="video">
    <iframe width="640" height="360" src="https://www.youtube.com/embed/V8S591S-eU0?feature=oembed" frameborder="0" allow="autoplay; encrypted-media" allowfullscreen=""></iframe>
  </div>
  <div class="quote">
    This is the quote for the video, which was taken from youtube.
  </div>
</div>

sg-wrapper:
<section>
  <div class="container">
    <div class="content-grid">
      <sg-wrapper-content/>
    </div>
  </div>
</section>

Styleguide 5.1
*/

.video-comp {
  background: $black;
  margin: 0;

  .video-component {
    padding: 40px 30px;

    @include grid-media($lg) {
      height: 548px;
      padding: 40px 60px;

      .video {
        .plyr__video-embed, .plyr {
          position: static;
        }

        .plyr__video-embed {
          padding: 0px;
        }
      }
    }


    .heading {
      @include grid-column(12, $no-gutter);

      @include grid-media($lg) {
        @include grid-column(2, $no-gutter);

        height: 460px;
        margin-right: 60px;
      }


      position: relative;

      h1 {
        color: $white;
        width: 100%;
        top: 0;
        margin: 0;
        margin-bottom: 25px;

        @include type-custom($color: $white, $font-weight: $font-bold, $font-size: 32px, $line-height: 1);

        @include grid-media($lg) {
          position: absolute;
          margin-bottom: 0;
        }
      }
    }

    .video {
      @include grid-column(12, $no-gutter);

      padding-top: 20px;

      @include grid-media($lg) {
        @include grid-column(6, $no-gutter);

        padding-top: 0px;

        // padding-left: 20px;
        height: 460px;

        .plyr {
          max-height: 460px;
        }
      }


      position: relative;

      &:hover {
        .plyr--playing {
          .plyr__play-large {
            opacity: 1;
            visibility: visible;
          }
        }
      }

      iframe {
        width: 100%;
        height: 100%;
        margin-bottom: 25px;

        @include grid-media($lg) {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);

          // padding: 20px;
          margin-bottom: 0;
        }
      }
    }

    .quote {
      @include grid-column(12, $no-gutter);

      padding-top: 20px;

      @include grid-media($lg) {
        @include grid-column(2, $no-gutter);

        padding-top: 0px;
        padding-left: 80px;
        height: 460px;
      }


      position: relative;

      p {
        @include type-custom($color: $white, $font-weight: $font-bold, $font-size: 14px, $line-height: 1.3);

        width: 100%;
        bottom: 0;
        margin: 0;
        padding-left: 30%;

        @include grid-media($lg) {
          position: absolute;
          padding-left: 0;
        }


        &:after {
          content: "";
          display: block;

          @include grid-column(10, $no-gutter);

          float: none;
          height: 2px;
          background: white;
          margin-top: 20px;
        }
      }
    }
  }
}

.video {
  width: 100%;
  height: 100%;
  position: relative;
}

iframe {
  width: 100%;
}